/* src/components/WorkoutList.css */
.workout-list {
  margin-top: 30px;
  text-align: center;
}

.workout-list ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  max-width: 700px;
}

.workout-card {
  background: #ffffff;
  margin-bottom: 15px;
  padding: 15px;
  border-left: 6px solid #00cec9;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.2s ease;
}

.workout-card:hover {
  transform: scale(1.01);
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.15);
}

.card-text {
  text-align: left;
}

.card-buttons button {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  margin-left: 10px;
  transition: transform 0.2s ease;
}

.card-buttons button:hover {
  transform: scale(1.2);
}

input[type="text"],
input[type="number"] {
  padding: 5px;
  margin: 3px;
  border: 1px solid #ccc;
  border-radius: 6px;
  width: 100px;
}


/* src/components/WorkoutList.css */
.workout-list {
  text-align: center;
  margin-bottom: 40px;
}

.workout-list ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  max-width: 700px;
}

.workout-card {
  background: #ffffff;
  margin-bottom: 15px;
  padding: 20px;
  border-left: 6px solid var(--primary-color);
  border-radius: 15px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.05);
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.2s ease;
}

.workout-card:hover {
  transform: scale(1.01);
  box-shadow: 0 18px 30px rgba(0, 0, 0, 0.08);
}

.card-text {
  text-align: left;
  line-height: 1.5;
  font-size: 15px;
}

.card-buttons button {
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  margin-left: 10px;
  transition: transform 0.2s ease;
}

.card-buttons button:hover {
  transform: scale(1.25);
}
