/* src/index.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root {
  --primary-color: #00b894;
  --secondary-color: #0984e3;
  --bg-gradient: linear-gradient(to right, #f9f9f9, #f0f4ff);
  --text-dark: #2d3436;
  --card-bg: #ffffff;
  --card-shadow: 0 15px 40px rgba(0, 0, 0, 0.05);
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background: var(--bg-gradient);
  color: var(--text-dark);
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 40px 20px;
  box-sizing: border-box;
 
}

.app {
  width: 100%;
  max-width: 1000px;
  background: var(--card-bg);
  padding: 40px 30px;
  border-radius: 20px;
  box-shadow: var(--card-shadow);
  animation: fadeIn 0.6s ease;
}

h1 {
  font-size: 2.8rem;
  text-align: center;
  margin-bottom: 30px;
  font-weight: 600;
  color: var(--text-dark);
}

h2, h3 {
  color: var(--text-dark);
  margin-bottom: 15px;
  font-weight: 500;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Scrollbar for lists (optional) */
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 4px;
}


input, select {
  font-family: 'Poppins', sans-serif;
}
.dashboard {
  text-align: center;
  background: #f1f2f6;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 6px 14px rgba(0,0,0,0.05);
}

.dashboard p {
  margin: 8px 0;
  font-weight: 500;
}

